.faq-hack-section{
    margin: 0;
    background:  #8c2d71;
     width: 100%;
     height: 100%;
     padding: 100px 50px 0px;
     overflow-y: scroll;
}

.faq-hack-section h1{
    margin-top: 30px;
    color: #F86B64;
    font-size: 40px;
}
.faq-hack-section p{
    font-size: 20px;
}
.hack-faqs{
    width: 80%;
    margin: 0 auto;
}
.hack-faqs .card{
    background: #ED3F58;
}
.hack-faqs .card-header{
    background: #F86B64;
}

.hack-faqs  .card-link .card-header{
   color:  #070206;
}

.hack-faqs .card-body{
    background: #fa9b96 ;
}

.hack-faqs .card-header:after{
 font-family: 'FontAwesome';
 content: "\f107";
 float: right;
}



@media only screen and (max-width: 1450px){
    .faq-hack-section{
        padding-top: 60px;
    }
    .hack-faqs{
        width: 100%;
    }
}

@media only screen and (max-width: 1150px){
    .faq-hack-section{
        padding-top: 30px;
    }
    .faq-hack-section h1{

        font-size: 30px;
    }
    .faq-hack-section p{
        font-size: 18px;
    }
}